import React,{useState,useEffect} from 'react';
import {FaBars} from 'react-icons/fa';
import {IconContext} from 'react-icons/lib';

import {
  Nav,
  NavbarContainer, 
  NavLogo,
  MobileIcon,
  NavMenu,
  NavItem,
  Navlinks,
} from './NavbarElements';
import {animateScroll as scroll} from 'react-scroll'

const Navbar = ({toggle,darkNav}) => {

  const[scrollNav,setScrollNav] = useState(false)

  const changeNav = () => {
      if(window.scrollY >= 80 ){
        setScrollNav(true);
      }
      else{
        setScrollNav(false);
      }
  }

  useEffect(() => {
    window.addEventListener('scroll',changeNav)
  },[])

  const toggleHome = () => {
    scroll.scrollToTop();
  }

    return (
      <>
      <IconContext.Provider value ={{color:'#fff'}}>
        <Nav scrollNav ={scrollNav}>
            <NavbarContainer>
                <NavLogo to="/" onClick ={toggleHome}>BLOOD</NavLogo>
                <MobileIcon onClick = {toggle}>
                  <FaBars />
                </MobileIcon>
                <NavMenu>
                  <NavItem>
                    <Navlinks to='plasma'
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}  
                    >PLASMA DONOR</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="requirement" 
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}
                    >REQUIREMENT</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="OxygenService" 
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}
                    >OXYGEN</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="RemdesivirService" 
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}
                    >REMDESIVIR</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="AmbulanceService" 
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}
                    >AMBULANCE</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="OtherServices" 
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-80}
                    >FOOD</Navlinks>
                  </NavItem>
                  <NavItem>
                    <Navlinks to="consultant" 
                    smooth={true} 
                    duration={500} 
                    spy="true" 
                    exact='true' 
                    offset={-80}
                    >CONSULTANT</Navlinks>
                  </NavItem>
                  </NavMenu>
                
            </NavbarContainer>    
        </Nav> 
      </IconContext.Provider>     
      </>
    )
}

export default Navbar;
