import React from 'react';
import './AdminPage.css'
import SidebarOption from './SidebarOption';
import HomeIcon from '@material-ui/icons/Home';
import LocalHospitalIcon from '@material-ui/icons/LocalHospital';
import HealingIcon from '@material-ui/icons/Healing';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';  
import DonorDataTable from './DonorDataTable';
import RecipientDataTable from './RecipientDataTable';
import Profile from '../../images/AdminProfile.png';
import { Avatar } from '@material-ui/core';


const AdminPage = ({heading,subline,DataTable}) => {
    return (
        <div className="adminPage">
            {/* Sidebar */}
            <div className="admin_left">
                <h1 className="sidebar_logo">BLOOD</h1>
                <br/>
                <hr/>
                <SidebarOption Icon={HomeIcon} title="Home" href="/"/>
                <SidebarOption Icon={LocalHospitalIcon} title="DonorList" href="/admin-donor"/>
                <SidebarOption Icon={HealingIcon} title="RecipientList" href="/admin-recipient" />
                <SidebarOption Icon={ExitToAppIcon} title="Logout" />
            </div>
            {/* Body */}
            <div className="admin_right">
                {/* Header */}
                <div className="Admin_header">
                    <div className="header_left">
                        <h2><strong>{heading}</strong></h2>
                        <p>{subline}</p>
                    </div>
                    <div className="header_right"> 
                        <Avatar src={Profile} alt="AdminProfile" />  
                        <h4>KSHITIZ VAYA</h4>   
                    </div>
                </div>
                <br/>
                <hr/>
                {/* Information List */}
                <div className="body_list">
                    {
                        DataTable ? <DonorDataTable/> : <RecipientDataTable/>
                    } 
                </div>
            </div>
        </div>
    )
}

export default AdminPage;
