 import React,{useState}  from 'react';
import { Badge, Button, Col, Form ,Row} from 'react-bootstrap';
import './DFormElements.css';
import { Background } from './DFormElements';
import useForm from './useForm';
import validate from './validateInfo';

 const FormSignUp = ({submitForm}) => {
    const {handleChange,handleSubmit,values,errors} = useForm(submitForm,validate);
    const [checkbox, setCheckbox] = useState(false);

    const handleClick = (e) => {
        setCheckbox(!checkbox);
      };

    
     return (
         <>
            <Background>
            <div className = "FormContainer">
                <h1 className ="headingPrimary"> <Badge variant="danger">Donor Form</Badge></h1>
                <Form className ='FormWrapper' onSubmit={handleSubmit} noValidate>
                    <Row xs={12}>
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridFirstName">
                            <Form.Label>FirstName</Form.Label>
                            <Form.Control 
                                type="text"
                                placeholder="Enter Firstname"
                                name='firstname'
                                value={values.firstname}
                                onChange={handleChange}
                            />
                            {errors.firstname && <p>{errors.firstname}</p>}
                        </Form.Group>
                        
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridLastname">
                            <Form.Label>LastName</Form.Label>
                            <Form.Control 
                                type="text" 
                                placeholder="Enter Lastname"  
                                name='lastname' 
                                value={values.lastname}
                                onChange={handleChange}
                            />
                            {errors.lastname && <p>{errors.lastname}</p>}
                        </Form.Group>
                    </Row>

                    <Row xs={12}>
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridDOB">
                            <Form.Label>Date Of Birth</Form.Label>
                            <Form.Control 
                                type="date" 
                                name='dateOfBirth'
                                value={values.dateOfBirth}
                                onChange={handleChange}
                             />
                            {errors.dateOfBirth && <p>{errors.dateOfBirth}</p>}
                        </Form.Group>

                    
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridBloodGroup">
                            <Form.Label>Blood Group</Form.Label>
                            <Form.Control 
                                as="select" 
                                defaultValue="choose.." 
                                name='bloodGroup' 
                                required
                                value={values.bloodGroup}
                                onChange={handleChange}
                            >
                            <option>Choose..</option>
                            <option>O+</option>
                            <option>O-</option>
                            <option>A+</option>
                            <option>A-</option>
                            <option>B+</option>
                            <option>B-</option>
                            <option>AB+</option>
                            <option>AB-</option>
                            </Form.Control>
                            {errors.bloodGroup && <p>{errors.bloodGroup}</p>}
                        </Form.Group>
                        

                    </Row>

                    <Row xs={12}>
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridLastDonatedDate">
                            <Form.Label>Last Donated Date</Form.Label>
                            <Form.Control 
                                type="date"  
                                name='lastDonated' 
                                value={values.lastDonated}
                                onChange={handleChange} 
                            />
                            {errors.lastDonated && <p>{errors.lastDonated}</p>}
                        </Form.Group>
                    
                        <Form.Group as={Col} xs={6} className="form-input mb-3" controlId="formGridGender">
                            <Form.Label>Gender</Form.Label>
                            <Form.Control 
                                as="select" 
                                defaultValue="choose.."  
                                name='gender'
                                value={values.gender}
                                onChange={handleChange} 
                                required
                            >
                            <option>Choose..</option>
                            <option>Male</option>
                            <option>Female</option>
                            <option>Other</option>
                            </Form.Control>
                            {errors.gender && <p>{errors.gender}</p>}
                        </Form.Group>

                    </Row>

                    <Row xs={12} >
                        <Form.Group as={Col} xs={12} className="form-input mb-3" controlId="formGridEmail">
                            <Form.Label>Email</Form.Label>
                            <Form.Control 
                                name='email' 
                                type="email" 
                                placeholder="Enter email" 
                                value={values.email}
                                onChange={handleChange}
                            />
                            {errors.email && <p>{errors.email}</p>}
                        </Form.Group>

                        <Form.Group as={Col} xs={12} className="form-input mb-3"  controlId="formGridDonor">
                            <Form.Label>Plasma/Blood Donor</Form.Label>
                            <Form.Control 
                                as="select" 
                                defaultValue="choose.." 
                                name='donorOption'
                                value={values.donorOption}
                                onChange={handleChange} 
                                required>
                            <option>Choose..</option>
                            <option>Plasma</option>
                            <option>Blood</option>
                            <option>Plasma and Blood Both</option>
                            </Form.Control>
                            {errors.donorOption && <p>{errors.donorOption}</p>}
                        </Form.Group>
                    </Row>
                    
                        <Form.Group className="form-input mb-3" controlId="formGridAddress1">
                            <Form.Label>Address</Form.Label>
                            <Form.Control  
                                name ='address'
                                placeholder="1234 Main St"
                                value={values.address}
                                onChange={handleChange} 
                            />
                            {errors.address && <p>{errors.address}</p>}
                        </Form.Group>

                        <Form.Group className="form-input mb-3" controlId="formGridPhoneNumber">
                            <Form.Label>Phone Number</Form.Label>
                            <Form.Control  
                                name ='phoneNumber' 
                                type="tel"
                                placeholder="+91-xxxxxxxxxx"
                                pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}"
                                value={values.phoneNumber}
                                onChange={handleChange}  
                            />
                            {errors.phoneNumber && <p>{errors.phoneNumber}</p>}
                        </Form.Group>
                    
                    <Row>
                        <Form.Group className="form-input mb-3" as={Col} controlId="formGridZip">
                            <Form.Label>Pincode</Form.Label>
                            <Form.Control 
                                name ='pincode'
                                value={values.pincode}
                                onChange={handleChange}
                             />
                            {errors.pincode && <p>{errors.pincode}</p>}
                        </Form.Group>

                        <Form.Group className="form-input mb-3" as={Col} controlId="formGridCity">
                            <Form.Label >City</Form.Label>
                            <Form.Control 
                                name='city'
                                value={values.city}
                                disabled
                            />
                            {errors.city && <p>{errors.city}</p>}
                        </Form.Group>

                        <Form.Group className="form-input mb-3" as={Col} controlId="formGridState">
                            <Form.Label>State</Form.Label>
                            <Form.Control 
                                name='state'
                                value={values.state}
                                disabled
                            >
                            </Form.Control>
                            {errors.state && <p>{errors.state}</p>}
                        </Form.Group>

                    </Row>
                    
                        <Form.Group id="formGridCheckbox" className="mb-3" >
                        <Form.Check type="checkbox" label="I agree that I have provided the correct details and Share my consent to receive a notification." onClick={handleClick} />
                        </Form.Group>
                        
                        <div className="d-flex justify-content-center">
                        <Button disabled={!checkbox} className ='btn-Register' variant="primary" type="submit"  >
                            Register
                        </Button>
                        </div>
                        
                </Form>
            </div>
            </Background>
        </>
     )
 }
 
 export default FormSignUp;
 