import React from 'react';
import { FaFacebook,FaInstagram,FaTwitter,FaYoutube,FaLinkedin } from "react-icons/fa";
import {animateScroll as scroll} from 'react-scroll';
import{
    FooterContainer,
    FooterWrap,
    FooterLinksContainer,
    FooterLinksWrapper,
    FooterLinkItems,
    FooterLinkTitle,
    FooterLink,
    SocialMedia,
    SocialMediaWrap,
    SocialLogo,
    WebsiteRights,
    SocialIcons,
    SocialIconLink
} from './FooterElements'

export const Footer = () => {

    const toggleHome = () => {
        scroll.scrollToTop();
      }

    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>About Us</FooterLinkTitle>
                                <FooterLink to ='/'>How It works</FooterLink>
                                <FooterLink to ='/'>Testimonials</FooterLink>
                                <FooterLink to ='/'>Carrer</FooterLink>
                                <FooterLink to ='/'>Investors</FooterLink>
                                <FooterLink to ='/'>Terms Of Service</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>Videos</FooterLinkTitle>
                                <FooterLink to ='/'>Submit Video</FooterLink>
                                <FooterLink to ='/'>Ambassador</FooterLink>
                                <FooterLink to ='/'>Agency</FooterLink>
                                <FooterLink to ='/'>Influencer</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>Contact Us</FooterLinkTitle>
                                <FooterLink to ='/'>Conatct</FooterLink>
                                <FooterLink to ='/'>Support</FooterLink>
                                <FooterLink to ='/'>Destination</FooterLink>
                                <FooterLink to ='/'>Sponsorships</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>ADMIN</FooterLinkTitle>
                                <FooterLink to ='/login'>Login</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialMediaWrap>
                        <SocialLogo to ="/" onClick = {toggleHome}>
                            Blood
                        </SocialLogo>
                        <WebsiteRights>Blood &copy; {new Date().getFullYear()} All rights reserved.</WebsiteRights>
                        <SocialIcons>
                        <SocialIconLink href ='/' target="_blank" aria-label="Facebook"><FaFacebook/></SocialIconLink>
                        <SocialIconLink href ='/' target="_blank" aria-label="Instagram"><FaInstagram/></SocialIconLink>
                        <SocialIconLink href ='/' target="_blank" aria-label="Youtube"><FaYoutube/></SocialIconLink>
                        <SocialIconLink href ='/' target="_blank" aria-label="Twitter"><FaTwitter/></SocialIconLink>
                        <SocialIconLink href ='/' target="_blank" aria-label="Linkedin"><FaLinkedin/></SocialIconLink>

                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer;