import React , {useEffect,useState} from 'react';
import MaterialTable from 'material-table';
import getToken from '../token/getToken';
import axios from 'axios';

const DonorDataTable = () => {
  const[data,setData] = useState([]);

  
  useEffect(() => {
    const accesstoken = "Bearer " + getToken();
    var config = {
      method: 'get',
      url: '/api/donors',
      headers: { 
        'Content-Type': 'application/json', 
        'Access-Control-Allow-Origin': '*',
        'mode': 'cors',
        'Authorization': accesstoken
      }
    };
    axios(config)
    .then(function (response) {
        setData(response.data);
    })
    .catch(function (error) {
      console.log(error);
    });

},[]);

  const columns = [
    { title: 'S.No', field: 'id',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'FirstName', field: 'firstName',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'LastName', field: 'lastName',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Blood Group', field: 'bloodGroupId.bloodGroup',cellStyle: {whiteSpace: 'nowrap'} },
    { title: 'Donor Option', field: 'donorOption',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Last Donated', field: 'lastDonationDate' ,cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Gender', field: 'gender' ,cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Phone Number', field: 'phoneNumber' ,cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Email', field: 'email',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'Address', field: 'address',cellStyle: {whiteSpace: 'nowrap'} },
    { title: 'Pincode', field: 'pincodeId.pincode',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'City', field: 'pincodeId.city',cellStyle: {whiteSpace: 'nowrap'}},
    { title: 'State', field: 'pincodeId.state',cellStyle: {whiteSpace: 'nowrap'}},
  ];

  

  return (
    <div>
      <MaterialTable
        title="Donor Table"
        data={data}
        columns={columns}
        options={{
          exportButton:true,
        }}
      />
    </div>
  )
}

export default DonorDataTable;



