import  { useState,useEffect} from 'react';
import axios from 'axios';

const useForm = (callback,validate)=> {
    const [values,setValues] = useState({
        firstname:"",
        lastname:"",
        dateOfBirth:"",
        bloodGroup:"",
        gender:"",
        phoneNumber:"",
        email:"",
        requirement:"",
        address:"",
        message:"",
        pincode:"",
        city:"",
        state:"",
    })

    const [errors,setErrors] = useState({});
    const [isSubmitting,setIsSubmitting] = useState(false);


    const handleChange = e => {
        const {name,value} = e.target
        if(name === "pincode" && Object.keys(value).length === 6){
          async function fetchData(){
            const request = await  axios.get(`https://api.postalpincode.in/pincode/${value}`);
            if(request.data[0].Status === "Success"){
              setValues({
                ...values,
                pincode:value,
                city:request.data[0].PostOffice[0].District,
                state:request.data[0].PostOffice[0].State,
                
              })
             } else{
                alert("Pincode is Invalid") 
              }
        }
        fetchData();    
        }
        setValues({
            ...values,
            [name]:value
        });
    };

    const handleSubmit = e => {
        e.preventDefault();
        setErrors(validate(values));
        setIsSubmitting(true);

        var config = {
          method: 'post',
          url: '/api/recipients',
          headers: { 
            'Access-Control-Allow-Origin': '*',
            'mode': 'cors',
            'Content-Type': 'application/json'
          },
          data : JSON.stringify(values)
        };
        
        axios(config)
        .then(function (response) {
          JSON.stringify(response.data);
        })
        .catch(function (error) {
          console.log(error);
        });
        
      };
  

    useEffect(
        () => {
            console.log(`errors`, errors)
          if (Object.keys(errors).length === 0 && isSubmitting) {
            callback();
          }
        },
        [errors]
      );
    

    return {handleChange,handleSubmit,values,errors};
};
export default useForm;