import React,{useState} from 'react';
import { Button } from '../ButtonElements';
import Video from "../../videos/video1.mp4";
import {
    HeroContainer,
    HeroBg,
    VideoBg,
    HeroContent,
    HeroH1,
    HeroP,
    HeroBtnwrapper,
    ArrowForward,
    ArrowRight
}
from "./HeroElements"

export const HeroSection = () => {

    const[hover,setHover] = useState(false);

    const onHover =() => {
        setHover(!hover);
    }

    return (
        <HeroContainer id = 'home'>
            <HeroBg>
                <VideoBg autoPlay loop muted src ={Video} type ='video/mp4' />
            </HeroBg>
            <HeroContent>
                <HeroH1>Blood Donation Is A Great Act Of Kindness </HeroH1>
                <HeroP>
                    Sign up to Become A Donor / Plasma Donor
                </HeroP>
                <HeroBtnwrapper>
                    <Button to="donor-form" 
                    onMouseEnter = {onHover} 
                    onMouseLeave={onHover} 
                    primary = 'true' 
                    dark = 'true'  
                    smooth={true} 
                    duration={500} 
                    spy={true} 
                    exact='true' 
                    offset={-90}
                    style={{textDecoration : "none"}}
                    >
                        Register{hover ? <ArrowForward/> : <ArrowRight/>}
                    </Button>
                </HeroBtnwrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection; 