import React , {useState,useEffect} from 'react';
import MaterialTable from 'material-table'
import getToken from '../token/getToken';
import axios from 'axios';

const RecipientDataTable = () => {
    const[data,setData] = useState([]);

    useEffect(() => {
      const accesstoken = "Bearer " +getToken();
      var config = {
        method: 'get',
        url: '/api/recipients',
        headers: {   
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*', 
          'mode': 'cors',
          'Authorization': accesstoken
        }
      };
      
      axios(config)
      .then(function (response) {
        setData(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
      
  },[]);
  

    const columns = [
      { title: 'S.No', field: 'id',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'FirstName', field: 'firstName',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'LastName', field: 'lastName',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'Required Blood Group', field: 'requiredBloodGroupId.bloodGroup',cellStyle: {whiteSpace: 'nowrap'} },
      { title: 'Requirement', field: 'requirement',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'Gender', field: 'gender' ,cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'Phone Number', field: 'phoneNumber' ,cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'Email', field: 'email',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'Address', field: 'address',cellStyle: {whiteSpace: 'nowrap'} },
      { title: 'Message' , field: 'message',cellStyle: {whiteSpace: 'nowrap'} },
      { title: 'success' , field: 'success',cellStyle: {whiteSpace: 'nowrap'} },
      { title: 'Pincode', field: 'pincode.pincode',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'City', field: 'pincode.city',cellStyle: {whiteSpace: 'nowrap'}},
      { title: 'State', field: 'pincode.state',cellStyle: {whiteSpace: 'nowrap'}},
    ];
  

  
    return (
      <div> 
        <MaterialTable
          title="Recipient Table"
          data={data}
          columns={columns}
          options={{
            exportButton:true,
          }}
        />
      </div>
    )
  }

export default RecipientDataTable
