import React from 'react'
import{
    SidebarContainer,
    Icon,
    CloseIcon,
    SideBarWrapper,
    SideBarMenu,
    SideBarLink,
}
from './SideBarElements'

export const SideBar = ({isOpen ,toggle}) => {
    return (
        <SidebarContainer isOpen = {isOpen} toggle = {toggle}>
            <Icon onClick = {toggle}>
                <CloseIcon/>
            </Icon>
            <SideBarWrapper>
                <SideBarMenu>
                    <SideBarLink to="plasma" onClick = {toggle}>PLASMA</SideBarLink>
                    <SideBarLink to="requirement" onClick = {toggle}>PLASMA REQUIREMENT </SideBarLink>
                    <SideBarLink to="OxygenService" onClick = {toggle}>OXYGEN </SideBarLink>
                    <SideBarLink to="RemdesivirService" onClick = {toggle}> REMDESIVIR</SideBarLink>
                    <SideBarLink to="AmbulanceService" onClick = {toggle}> AMBULANCE </SideBarLink>
                    <SideBarLink to="OtherServices" onClick = {toggle}> FOOD</SideBarLink>
                    <SideBarLink to="consultant" onClick = {toggle}> CONSULTANT</SideBarLink>
                </SideBarMenu>
            </SideBarWrapper>

        </SidebarContainer>
           
    )
}

export default SideBar;