import React,{useState} from 'react';
import HeroSection from '../components/HeroSection';
import { InfoSection } from '../components/infoSection';
import { Footer } from '../components/Footer';
import { 
    homeObjOne,
    homeObjTwo ,
    homeObjThree } from '../components/infoSection/Data';
import Navbar from "../components/Navbar";
import { Services } from '../components/OxygenServices';
import { RemdesivirServices } from '../components/RemdesivirService';
import { AmbulanceServices } from '../components/AmbulanceService';
import SideBar from "../components/SideBar";
import OtherServices from '../components/Other Services';

export const Home = () => {
    const [isOpen,setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen)
    }   

 
    return (
        <div>
            <SideBar isOpen = {isOpen} toggle = {toggle}/>
            <Navbar toggle = {toggle}/>
            <HeroSection />
            <InfoSection {...homeObjOne} />
            <InfoSection {...homeObjTwo} />
            <Services />
            <RemdesivirServices />
            <AmbulanceServices />
            <OtherServices />
            <InfoSection {...homeObjThree} />
            <Footer />
        </div>
    )
}
