import React from 'react';
import {Button} from '../ButtonElements';
import { 
    InfoContainer,
    InfoWrapper,
    InfoRow,
    Column1,
    Column2,
    TextWrapper,
    TopLine,
    Heading,
    Subtitle,
    BtnWrap,
    ImgWrap,
    Img
} from './infoElements'

export const InfoSection = ({
    lightBg,
    id,
    imgStart,
    topLine,
    lightText,
    headline,
    darkText,
    description,
    buttonLabel,
    img,
    alt,
    primary,
    dark,
    dark2,
    link1
    }) => {
    return (
        <>
            <InfoContainer lightBg={lightBg} id={id}>
                <InfoWrapper>
                    <InfoRow imgStart = {imgStart}>
                        <Column2>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                <Heading lightText ={lightText}>{headline}</Heading>
                                <Subtitle darkText = {darkText}>{description}</Subtitle>
                                    <BtnWrap>
                                        <Button to={link1}
                                                smooth={true} 
                                                duration={100} 
                                                spy={true} 
                                                exact='true' 
                                                offset={-90}
                                                primary={primary ? 1 : 0}
                                                dark = {dark ? 1 : 0}
                                                dark2 = {dark2 ? 1 : 0}
                                                >{buttonLabel}
                                        </Button>                    
                                    </BtnWrap>
                            </TextWrapper>
                        </Column2>
                        <Column1>
                            <ImgWrap>
                                <Img src={img} alt={alt} />
                            </ImgWrap>
                        </Column1>
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>
        </>
    )
}

export default InfoSection;