import React from "react";
import { Route, Redirect } from "react-router-dom";
import getToken from './components/token/getToken';


const PrivateRoute = ({ component: Component, auth, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      getToken() != null ? (
        <Component {...props} />
      ) : (
        <Redirect to="/login" />
      )
    }
  />
);


export default PrivateRoute;