import './App.css';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import { Home } from './pages';
import Donor from './pages/donor';
import Recipient from './pages/recipient';
import recipientList from './pages/recipientList';
import login from './pages/loginPage';
import donorList from './pages/donorList';
import PrivateRoute from './PrivateRoute';
import 'react-app-polyfill/stable'

function App() {
  return (
    <Router>
      <Switch>
        <Route path ="/" component ={Home} exact />
        <Route path ="/donor-form" component ={Donor} exact />
        <Route path ="/recipient-form" component ={Recipient} exact />   
        
        <Route path = "/login" component={login} exact />
        <PrivateRoute path ="/admin-donor" component={donorList} exact/>
        <PrivateRoute path = "/admin-recipient" component={recipientList} exact />
      </Switch>
    </Router>
  );
}

export default App;
