export default function validateInfo(values){
    let errors = {};

    if(!values.firstname.trim()){
        errors.firstname = "Firstname Required"
    }

    if(!values.lastname.trim()){
        errors.lastname = "Lastname Required"
    }

    if(!values.dateOfBirth.trim()){
        errors.dateOfBirth = "Date Of Birth Required"
    }

    if(!values.bloodGroup.trim()){
        errors.bloodGroup = "Blood Group Required"
    }

    if(!values.lastDonated.trim()){
        errors.lastDonated = "Last Donated Date Required"
    }

    if(!values.gender.trim()){
        errors.gender = "Gender Required"
    }

    if(!values.email.trim()){
        errors.email = "Email Required"
    } else if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)){
        errors.email = "Email Address is invalid"
    }

    if(!values.donorOption.trim()){
        errors.donorOption = "Donor Option Required"
    }

    if(!values.address.trim()){
        errors.address = "Address Required"
    }

    if(!values.phoneNumber.trim()){
        errors.phoneNumber = " Mobile Number Required"
    }

    if(!values.pincode.trim()){
        
        errors.pincode = "Pincode Required"
    }


    if(!values.city.trim()){
        errors.city = "City Required"
    }

    if(!values.state.trim()){
        errors.state = "State Required"
    }

    return errors;

}